@import '../../scss/setup';

.loginContainer {
	@include rowVertically();
	padding: 70px 48px;
	background-color: $brown;
	box-shadow: -8px 8px 0px $lighten-gray;
	border: 2px solid $black;
	box-sizing: border-box;
	&__illustration {
		margin-right: 48px;
		flex-grow: auto;
	}
	&__form {
		align-self: flex-start;
		flex: 0 0 37.27959698%;
		&-logo {
			text-align: center;
			margin-bottom: 36px;
		}
		img {
			max-width: 296px;
		}
		h2 {
			font-size: 24px;
			font-weight: bold;
			color: $black;
		}
	}
}

.socialLoginContainer {
	@include rowVertically();
	margin-top: 8px;
	a {
		@include rowVertically();
		@include basicBorder();
		width: 48px;
		height: 48px;
		background-color: $white;
		border-radius: 50%;
		margin: 0 8px;
		svg {
			font-size: $font-xl;
			color: $black;
		}
	}
}
